body {
  background-image: url("wp153122.92d36ee1.jpg");
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

nav {
  background-color: #0041b1b3;
  border-top: 1px solid navy;
  width: 100%;
  height: 50px;
  padding: 5px 25px;
  position: fixed;
  bottom: 0;
}

nav .AppButton {
  float: left;
  background: 0 0 / 100px 50px no-repeat;
  border: 0;
  width: 50px;
  height: 50px;
  margin-right: 30px;
}

nav .AppButton:hover {
  cursor: pointer;
  background-position: -50px;
}

nav .ChatButton {
  background-image: url("double-chat-sprite.a5805477.png");
}

nav .MemoryButton {
  background-image: url("cards-sprite.4c478811.png");
}

nav .PaintButton {
  background-image: url("paint-sprite.0bf6164c.png");
}

nav .AboutButton {
  float: right;
  background-image: url("question-mark-sprite.188d7fb8.png");
}

.Window {
  border: 2px solid #0059f4;
  outline: 1px solid navy;
  min-width: 200px;
  display: inline-block;
  position: absolute;
}

.Window .WindowNav {
  color: #fff;
  background-color: #0059f4;
  height: 40px;
}

.Window .WindowNav .WindowIcon {
  float: left;
  background-position: -30px;
  background-size: 60px 30px;
  width: 30px;
  height: 30px;
  margin: 2px;
  position: relative;
}

.Window .WindowNav .WindowTitle {
  float: left;
  margin-top: 8px;
  margin-left: 5px;
  position: relative;
}

.Window .WindowNav .WindowClose {
  background-image: url("close-sprite.ceb764b6.png");
  background-size: 60px 30px;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 3px;
  right: 4px;
}

.Window .WindowNav .WindowClose:hover {
  cursor: pointer;
  background-position: -30px;
}

.Window .WindowNav:hover {
  cursor: default;
}

.Window .WindowContent {
  background-color: #f5f5f5;
  border: 1px solid navy;
  padding: 10px;
  overflow: auto;
}

.Window .WindowContent .Messages {
  background-color: #fff;
  border: 1px solid #000;
  height: 200px;
  margin-top: 15px;
  margin-bottom: 10px;
  overflow-y: scroll;
}

.Window .WindowContent .Messages p {
  margin: 10px;
}

.Window .WindowContent .Messages .Username {
  color: gray !important;
}

.Window .WindowContent textarea {
  float: left;
  width: 75%;
  height: 50px;
  margin-right: 10px;
  position: relative;
}

.Window .WindowContent input[type="button"] {
  float: right;
  height: 50px;
  position: relative;
}

.Window .WindowContent .MemoryImage {
  width: 100px;
  height: 100px;
}

.Window .WindowContent .Removed {
  visibility: hidden;
}

.Window .WindowContent a:focus {
  outline: 2px solid #006eff;
}

.Window .WindowContent .Sheet {
  background: #fff;
  outline: 2px solid #000;
  margin-top: 15px;
}

.Inactive {
  background-color: #68a0ff !important;
  border-color: #68a0ff !important;
}

.Inactive .WindowIcon {
  background-position: 0 0 !important;
}

